@import (reference) '~antd/lib/style/themes/default';

@pro-card-prefix-cls: ~'@{ant-prefix}-pro-card';

.@{pro-card-prefix-cls} {
  &-operation {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin: @margin-lg 0; //  size 
    color: rgba(0, 0, 0, 0.85);
    font-weight: 500;
    font-size: 20px;
    line-height: 38px;
  }
}

